import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { AsideComponent } from './components/aside/aside.component';
import { ProfileComponent } from './components/profile/profile.component';
import { AccessComponent } from './components/access/access.component';
import { ROUTES } from './app.routes';
import { MoviesComponent } from './components/profile/movies/movies.component';
import { ContactComponent } from './components/profile/contact/contact.component';
import { AlertComponent } from './components/profile/alert/alert.component';


@NgModule({
  declarations: [
    AppComponent,
    AsideComponent,
    ProfileComponent,
    AccessComponent,
    MoviesComponent,
    ContactComponent,
    AlertComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot( ROUTES )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



