import { Injectable } from '@angular/core';

@Injectable()
export class MoviesService {

  movies = [
    {
      title: 'The Wizard of Oz',
      director: 'Victor Fleming',
      year: '1939',
    },
    {
      title: 'The Third Man',
      director: 'Carol Reed',
      year: '1949',
    },
    {
      title: 'Citizen Kane',
      director: 'Orson Welles',
      year: '1941',
    },
  ];

  constructor() {
    console.log(this.movies);
  }

  returnMovies() {
    return this.movies;
  }
}



