import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
value: boolean;
Form: FormGroup;
  alertTextError: string;
  showAlert: boolean;
  alertError: boolean;
  alertTextRight: string;
  TimeAlert: boolean;
  alertSuccess: boolean;
  constructor() { 
    this.value = true;
    this.Form = new FormGroup({
      'name': new FormControl('', Validators.required),
      'address': new FormControl('',  Validators.required),
      'name1': new FormControl('', Validators.required),
      'name2': new FormControl('', ),
      'name3': new FormControl('', Validators.required),
      'name4': new FormControl('', ),
      'city': new FormControl('', ),
      'country': new FormControl('',  Validators.maxLength(3) ),
      'phone': new FormControl('',  [ Validators.required, Validators.pattern('^(\d{10})|(([\(]?([0-9]{3})[\)]?)?[ \.\-]?([0-9]{3})[ \.\-]([0-9]{4}))$')]),
      'state': new FormControl('', Validators.maxLength(2) ),
      'zip': new FormControl('',)
    });
  }

  ngOnInit() {
  }

  closeAlert() {
    this.showAlert = false;
  }
  form() {
    console.log('entro');
    if ( this.Form.valid ) {
      // Ready to post
      console.log(this.Form);
      console.log('valid');
      this.showAlert = true;
      this.alertError = true;
      this.TimeAlert = false;
      this.alertTextError = 'se hizo correctamente';
    } else {
      console.log('invalid');
      // Invalid login form
      this.showAlert = true;
      this.alertError = true;
      this.TimeAlert = false;
      this.alertTextError = 'algo salio mal';
      if (this.Form.controls['name'].invalid) {
        console.log('invalid1');
        const element = document.getElementById("name");
        element.classList.add("error");
      }
      if (this.Form.controls['address'].invalid) {
        console.log('invalid2');
        const element = document.getElementById("address");
        element.classList.add("error");
      }
      if (this.Form.controls['name1'].invalid) {
        console.log('invalid3');
        const element = document.getElementById("name1");
        element.classList.add("error");
      }
      if (this.Form.controls['name3'].invalid) {
        console.log('invalid4');
        const element = document.getElementById("name3");
        element.classList.add("error");
      }
      if (this.Form.controls['country'].invalid) {
        console.log('invalid5');
        const element = document.getElementById("country");
        element.classList.add("error");
      }
      if (this.Form.controls['state'].invalid) {
        console.log('invalid6');
        const element = document.getElementById("state");
        element.classList.add("error");
      }
      if (this.Form.controls['phone'].invalid) {
        console.log('invalid7');
        const element = document.getElementById("phone");
        element.classList.add("error");
      }
    }
  }
  radio(event) {
    if(event.target.id === 'home'){
      this.value = false;
      const element = document.getElementById("homeSpan");
      element.classList.remove("white");
      const element1 = document.getElementById("workSpan");
      element1.classList.add("white");
    } else if(event.target.id === 'work'){
      this.value = true;
      const element = document.getElementById("homeSpan");
      element.classList.add("white");
      const element1 = document.getElementById("workSpan");
      element1.classList.remove("white");
    }
    console.log(this.value);
  }
}



