import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.scss']
})
export class AlertComponent {

  @Input() alertSuccess: boolean;
  @Input() alertError: boolean;
  @Input() alertTextRight: string;
  @Input() alertTextError: string;
  @Input() widthAlert: boolean;
  @Input() timeAlert: boolean;
  @Output() closeAlert: EventEmitter<boolean>;
  constructor() {
    
    this.closeAlert = new EventEmitter();
   }

  ngOnInit() {
  }

  clickClose() {
    this.restartAlert();
    this.closeAlert.emit(true);
  }

  automaticClose() {
  setTimeout(() => {
    this.clickClose();
  }, 3000 );
  }

  restartAlert() {
    this.alertError = false;
    this.alertSuccess = false;
  }
}



